<template>
	<view class="">
		<view style="height: 90rpx;">
			<view class="toptitle">
				<text>{{title||'未填写'}}</text>
			</view>
		</view>
		<swiper v-if="bannerList.imgFileModelId" :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item  @tap='PreviewPic(item,bannerList.previewImage)' v-for="(item,index) in bannerList.previewImage" :key='index'>
				<view class="swiper-item">
					<image :src='item' mode="aspectFit" @error="error('bannerList.previewImage',index)"></image>
				</view>
			</swiper-item>	
		</swiper>
		<swiper v-else :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item>
				<view class="swiper-item">
					<image src='../../static/image/default.png' mode="aspectFit"></image>
				</view>
			</swiper-item>	
		</swiper>
		<view class="docInfor" v-for="(item,index) in docinfor" :key='index'>
			<image :src='item.doctorVO.avatarLinks' mode=""></image>
			<view class="docInfor_content">
				<view class="">
					<text class="docname">{{item.doctorVO.name}}</text>
					<view class="infor">
						<text>{{item.doctorVO.department}}</text>
						<text class="line">|</text>
						<text>{{item.doctorVO.hospital}}</text>
					</view>
				</view>
				<view class="infor">
					<text>{{time}}</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data(){
			return{
				Imageplay:true,//轮播图是否自动切换,true自动切换
				src:'../../static/images/default.png'
			}
		},
		methods:{
			// 图片错误时
			error(e,key,index){
				console.log(e)
				console.log(key)
				this[key][index].image = this.src;
			},
			PreviewPic(data){
				console.log(data)
			}
		},
		props:{
			title:{
				type:String,
				default:""
			},
			bannerList:{
				type:Object,
				default:()=>{
					return{}
				},
			},
			docinfor:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			time:{
				type:String,
				default:""
			}
		},
	}
</script>

<style scoped>
	.toptitle{
		padding: 26rpx 0;
		background-color: #FFFFFF;
		text-align: center;
		position: fixed;
		width: 100%;
		z-index: 100;
		color: #000000;
		font-family: PingFang-SC-Medium;
		border-top: 1px solid #DEDEDE;
		border-bottom: 1px solid #DEDEDE;
		font-weight: 500;
	}
	.swiper-item image{
		width: 100%;
		height: 420rpx;
		background: #FFFFFF;
	}
	uni-swiper{
		height: 420rpx!important;
	}
	.docInfor{
		display: flex;
		padding: 30rpx;
		align-items: flex-start;
		border-bottom: 1px solid #F0EFEF;
		background-color: #FFFFFF;
	}
	.docInfor uni-image{
		width: 100rpx;
		height: 100rpx;
		border-radius: 50%;
		border: 1px solid #F0EFEF;
		margin-right: 23rpx;
	
	}
	.docInfor .docname{
		font-size: 28rpx;
		color: #000000;
	}
	.docInfor_content{
		flex: 1;display: flex;justify-content: space-between;
	}
	.infor .line{
		margin: 0 10rpx;
	}
	.docInfor .infor{
		font-size: 24rpx;
		line-height: 70rpx;
		color: #8F8F8F;
	}
</style>
